import React from 'react'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

function Posts() {

    const [posts, setPosts] = useState([])

    useEffect(() => {
        axios.get('/posts-api/posts/').then(res => {
            setPosts(res.data)
            // console.log(posts)
        })
    }, [])




    return (
        <div>
            <h1 className="text-left mb-4 mt-4">Social Posts</h1>

            {posts.map(post => (
                <div className="container">
                    <div className="card mb-3" style={{width: '100%'}}>
                        <div className="card-body">
                            <p className="lead card-title">{post.title}</p>
                            <p className="card-text">{post.body.substr(0, 30)}...</p>
                            <p className="card-text">By: {post.by}</p>
                            <p className="card-text">Created at: {new Date("Y-m-d H:i:s",  post.created_at)}</p>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Posts
